import {Link} from 'react-router-dom';
import React,{ useState } from 'react';
import LandingPage from '../pages/LandingPage';

function Teacher(){
    const [isOpen, setIsOpen] = useState(false)

    const toggleChat = ()=>{
        setIsOpen(!isOpen);
    }
    return(
       <div className="teacher-sign-up w-full h-full relative">
        <LandingPage/>
        <div className="h-full w-screen bg-black absolute top-0 z-30 opacity-70"></div>
        <div className="h-full w-screen z-40 absolute top-0 flex justify-center items-center">
                <div className="modal w-96 h-96 px-3">
                    <div className="modal-header w-full h-7 bg-secondary text-lightGray flex items-center justify-end">
                        <Link to={'/'}>
                            <i className="fa fa-close cursor-pointer mr-2 text-lg transisiton ease-in-out duration-150 hover:text-gray-400"></i>
                        </Link>
                    </div>
                    <div className="modal-body w-auto h-full bg-lightGray text-gray-700 relative overflow-y-auto scrollbar scrollbar-thumb-gray-500 scrollbar-thin scrollbar-track-transparent">
                        <form autocomplete="off">
                            <div className={isOpen ? "hidden":"personal_info w-full h-auto grid place-items-center gap-4 absolute text-sm pb-10 "}>
                                <label className="pt-6 text-lg pb-2">Personal Information</label>
                                <input type="text" name="teacher_id" className="w-60 px-4 py-2 outline-none bg-gray-300" placeholder="Teacher's ID"/>
                                <input type="text" name="teacher_name" className="w-60 px-4 py-2 outline-none bg-gray-300" placeholder="Teacher's Name"/>
                                <input type="text" name="teacher_contact" className="w-60 px-4 py-2 outline-none bg-gray-300" placeholder="Contact No."/>
                                <select className="w-60 px-4 py-2 outline-none bg-gray-300" name="school_type">
                                    <option value="" className="bg-white">Type of School</option>
                                    <option value="primary" className="bg-white">Primary</option>
                                    <option value="secondary" className="bg-white">Secondary</option>
                                </select>
                                <select className="w-60 px-4 py-2 outline-none bg-gray-300" name="school_name">
                                    <option value="" className="bg-white">School</option>
                                    <option value="primary" className="bg-white">TOCS-SSES</option>
                                </select>
                                <input type="text" name="school_id" className="w-60 px-4 py-2 outline-none bg-gray-300" placeholder="School ID"/>
                                <label onClick={toggleChat} className="w-60 p-2 mt-2 cursor-pointer text-center bg-secondary text-lightGray text-sm outline-none border-none transisiton ease-in-out duration-200 hover:bg-primary">Next</label>
                            </div>
                            <div className={isOpen? "account_info w-full h-auto grid place-items-center gap-4 absolute pb-10 text-sm":"hidden"}>
                               <label className="pt-6 text-lg pb-2">Account Information</label>
                               <input type="text" name="teacher_id" className="w-60 px-4 py-2 outline-none bg-gray-300" placeholder="Username"/>
                                <input type="text" name="teacher_name" className="w-60 px-4 py-2 outline-none bg-gray-300" placeholder="Email address"/>
                                <input type="text" name="teacher_contact" className="w-60 px-4 py-2 outline-none bg-gray-300" placeholder="Password"/>
                                <input type="text" name="teacher_id" className="w-60 px-4 py-2 outline-none bg-gray-300" placeholder="Confirm Password"/>
                                <button className="w-60 p-2 mt-2 bg-secondary text-center text-lightGray text-sm outline-none border-none transisiton ease-in-out duration-200 hover:bg-primary">Register</button>
                                <label onClick={toggleChat} className="w-60 p-2 cursor-pointer text-center bg-secondary text-lightGray text-sm outline-none border-none transisiton ease-in-out duration-200 hover:bg-primary">Go Back</label>
                            </div>
                        </form>
                    </div>
                    <div></div>
                </div>
            </div>
       </div>
    )
}

export default Teacher;